
div.title{
    position: absolute;
    top:10%;
    left:50%;
    width: 100%;
    transform: translateX(-50%);
    text-align: center;
    animation: title 1.5s linear;
    font-size: 1.5vw;
  }

div.icons{
    position:absolute;
    display: flex;
    top:50%;
    left:0;
    transform: translateY(-50%);
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    height: 30vh;
    width: 100%;
    overflow: hidden;
  }
  
  div.icons span{
    font-family: 'Special Elite', sans-serif;
    font-weight: bold;
    font-size: 5vh;
    color: black;
  }
  
  div.icons i {
    display: block;
    text-align: center;
    margin-bottom: 15px;
    font-size: 10vh;
    animation: rotate 5s linear infinite alternate ;
}

.scrollLabel i {
  display: none;
}



@keyframes rotate{
  0%{
    transform: perspective(100px) rotateY(-30deg);
  }
  100%{
    transform: perspective(100px) rotateY(30deg);
  }
}

@keyframes title{
  0%{
    transform:translateX(-50%) scale(0);
  }
  100%{
    transform:translateX(-50%) scale(1);
  }
}


/*------------------------ Media Queries--------------------------- */

@media(orientation: portrait){
  
div.title{
  font-size: 1.6vh;
  }
  
div.icons{
  flex-direction: column;
  height: 48vh;
  width: 85vw;
  top:55%;
  left:50%;
  transform: translate(-50%, -50%);
  justify-content: flex-start;
  flex-wrap: nowrap;
  border-radius: 50%;
  box-shadow: 0px 0px 20px 2px black;
  overflow-y: scroll;
  scrollbar-width: none;
}

::-webkit-scrollbar{
  display:none;
  }

div.icons span:nth-child(1){
  margin-top:150px;
}

div.icons span{
  margin-bottom:150px;
  font-size: 5vh;
}

div.icons i {
  font-size: 10vh;

}

.scrollLabel i {
  position: absolute;
  display: block;
  top: 67%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  text-shadow: 0px 13px 4px rgba(0,0,0,0.3);
  animation: scroll1 1.5s infinite linear ;
}

.scrollEnd.scrollLabel i {
  top: 33%;
  transform: translateX(-50%) rotate(180deg);
  text-shadow: 0px -13px 4px rgba(0,0,0,0.3);
  animation: scroll2 1.5s infinite linear ;
}

@keyframes scroll1{
  100%{
    top: 70%;
  }
}

@keyframes scroll2{
  100%{
    top: 30%;
  }
}

}

