/* burger animation */

:root {
    --animTime: 1.5s;
}

.burger {
    position: fixed;
    top: 25px;
    right: 25px;
    width: 55px;
    height: 35px;
    cursor: pointer;
    overflow: hidden;
    z-index: 2;
}

.burger span {
    position: absolute;
    background-color: darkred;
    width: 100%;
    height: 6px;
    border-radius: 28%;
    transition: background-color .2s linear;
}

.burger:hover span {
    background-color: red;
}

.burger span:nth-child(1) {
    left: 0;
    top: 0;
}

.burger.active span:nth-child(1) {
    -webkit-animation: slide_top 1.5s linear forwards;
            animation: slide_top 1.5s linear forwards;
    -webkit-animation: slide_top var(--animTime) linear forwards;
            animation: slide_top var(--animTime) linear forwards;
}

.burger.close span:nth-child(1) {
    animation: slide_top_rv 1.5s linear reverse;
    animation: slide_top_rv var(--animTime) linear reverse;
}

.burger span:nth-child(2) {
    top: calc(50% - 3px);
    left: 0;
    transition: opacity .5s linear, background-color .2s linear;
}

.burger.active span:nth-child(2) {
    opacity: 0;
}

.burger span:nth-child(3) {
    left: 0;
    bottom: 0;
}

.burger.active span:nth-child(3) {
    -webkit-animation: slide_bottom 1.5s linear forwards;
            animation: slide_bottom 1.5s linear forwards;
    -webkit-animation: slide_bottom var(--animTime) linear forwards;
            animation: slide_bottom var(--animTime) linear forwards;
}

.burger.close span:nth-child(3) {
    animation: slide_bottom_rv 1.5s linear reverse;
    animation: slide_bottom_rv var(--animTime) linear reverse;
}

@-webkit-keyframes slide_top {
    0% {
        left: 0px;
    }

    50% {
        top: 0;
        left: -60px;
        -webkit-transform-origin: 0% 50%;
                transform-origin: 0% 50%;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    75% {
        top: -60px;
        left: -60px;
        -webkit-transform-origin: 0% 50%;
                transform-origin: 0% 50%;
        -webkit-transform: rotate(33deg);
                transform: rotate(33deg);
    }

    100% {
        top: 0px;
        left: 5px;
        -webkit-transform-origin: 0% 50%;
                transform-origin: 0% 50%;
        -webkit-transform: rotate(33deg);
                transform: rotate(33deg);
    }
}

@keyframes slide_top {
    0% {
        left: 0px;
    }

    50% {
