div.cover{
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 220px;
    background: url(/Portfolio_website_developer_version/static/media/brush-stroke-banner.93993e10.svg);
