    background-position:center;
    -webkit-clip-path: inset(0px 220px 0px 0px);
            clip-path: inset(0px 220px 0px 0px);
    padding: 40px 70px;
    transition: .5s;
}


a:hover + .cover {
    
    -webkit-clip-path:inset(0px 0px 0px 0px);
    
            clip-path:inset(0px 0px 0px 0px);
}


nav ul li a {
    position:absolute;
    top:50%;
    left:50%;
